/* reset */
html{color:#000;background:#FFF;}body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{margin:0;padding:0;}table{border-collapse:collapse;border-spacing:0;}fieldset,img{border:0;}address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:normal;}li{list-style:none;}caption,th{text-align:left;}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;}q:before,q:after{content:'';}abbr,acronym{border:0;font-variant:normal;}sup{vertical-align:text-top;}sub{vertical-align:text-bottom;}input,textarea,select{font-family:inherit;font-size:inherit;font-weight:inherit;}input,textarea,select{*font-size:100%;}legend{color:#000;}

            ::-moz-selection {background: #f1d9e7; color: #000;}
            ::selection {background: #f1d9e7; color: #000;}

            /* slightly enhanced, universal clearfix hack */
            .clearfix:after {
                 visibility: hidden;
                 display: block;
                 font-size: 0;
                 content: " ";
                 clear: both;
                 height: 0;
                }
            .clearfix { display: inline-block; }
            /* start commented backslash hack \*/
            * html .clearfix { height: 1%; }
            .clearfix { display: block; }
            /* close commented backslash hack */

            body {color: #222; font-family: "Molengo", Arial, sans-serif; text-align: center; text-transform: lowercase;}

            .page {margin: 0 auto; text-align: left; width: 960px; padding: 0; position: relative;}



            code {text-transform: none; color: #000;}

            header {
                position: fixed;
                top:0;
                left: 0;
                right: 0;
                background: #efefef;
                border-bottom: 1px solid #EF308F;
                background: rgba(239,239,239,0.95);
                z-index: 110;
                padding: 10px 10px 0;
                display: block;
                -webkit-box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2) !important;
                -moz-box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2) !important;
                -o-box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2) !important;
                -khtml-box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2) !important;
                -ms-box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2) !important;
                box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2) !important;
                text-align: center;}
            header .content {margin: 0 auto; text-align: left; width: 940px; position: relative;}

            section {
                padding: 10px 10px 0;
                position: relative;
                display: block;
                clear: both;
                background: #efefef;
                border: 1px solid #e7e5e5;
                margin-bottom: 30px;
                -moz-border-radius: 0 0 5px 5px;
                -o-border-radius: 0 0 5px 5px;
                -khtml-border-radius: 0 0 5px 5px;
                -ms-border-radius: 0 0 5px 5px;
                border-radius: 0 0 5px 5px;
                -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.05);
                -moz-box-shadow: 0 0 20px rgba(0,0,0,0.05);
                -o-box-shadow: 0 0 20px rgba(0,0,0,0.05);
                -khtml-box-shadow: 0 0 20px rgba(0,0,0,0.05);
                -ms-box-shadow: 0 0 20px rgba(0,0,0,0.05);
                box-shadow: 0 0 20px rgba(0,0,0,0.05);
            }
            section.subSection {
                padding: 30px 0 10px;
                border: 0 none;
                margin-bottom: 0;
                border-bottom: 1px dashed rgba(0,0,0,0.1);
                -webkit-box-shadow: none;
                -moz-box-shadow: none;
                -o-box-shadow: none;
                -khtml-box-shadow: none;
                -ms-box-shadow: none;
                box-shadow: none;
            }
            section.subSection:last-child {border-bottom: 0 none;}
            section .marker {position: absolute; top: -68px;}

            .home {padding: 100px 0 10px;}

            .homepageInfo {
                font-size: 11px;
                color: #666;
                background: #fff;
                -webkit-border-radius: 3px;
                -moz-border-radius: 3px;
                -o-border-radius: 3px;
                -khtml-border-radius: 3px;
                -ms-border-radius: 3px;
                border-radius: 3px;
                text-align: center;
                margin: 0 6px;
                padding: 3px 0;
                margin-bottom: 10px;
           }

