            article {padding: 0 10px; display:block;}
            aside {margin-left: 20px; display:block;}

            .popOut {
                background: #fff;
                background-image: -moz-linear-gradient(-45deg, #fff, #ffe8f3);
                background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0,#fff), color-stop(1,#ffe8f3));
                border: 10px solid #fff;
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                -o-border-radius: 10px;
                -khtml-border-radius: 10px;
                -ms-border-radius: 10px;
                border-radius: 10px;
                padding: 10px;
                margin: 0 10px;
           }

            .homeSpeech {
                position: absolute;
                font-size: 20px;
                line-height: 110%;
                padding: 10px 15px 15px;
                left: -190px;
                top: 70px;
                width: 130px;
                color: #555;
                -webkit-box-shadow: 0 2px 5px rgba(0,0,0,0.2);
                -moz-box-shadow: 0 2px 5px rgba(0,0,0,0.2);
                -o-box-shadow: 0 2px 5px rgba(0,0,0,0.2);
                -khtml-box-shadow: 0 2px 5px rgba(0,0,0,0.2);
                -ms-box-shadow: 0 2px 5px rgba(0,0,0,0.2);
                box-shadow: 0 2px 5px rgba(0,0,0,0.2);
                border: 1px solid #EF308F;
                background: #FFE8F3;
                border-radius: 5px;
                display: block;
                text-shadow: 0 1px 0 #fff;
           }
            .csstransforms .homeSpeech:after {
                width: 12px;
                height: 12px;
                top: -7px;
                left: 50%;
                -webkit-transform: rotate(45deg);
                -moz-transform: rotate(45deg);
                -o-transform: rotate(45deg);
                -khtml-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
                box-shadow: none;
                border-radius: 0;
                border-left: 1px solid #EF308F;
                border-top: 1px solid #EF308F;
                content: "\00a0";
                display: block;
                position: absolute;
                background: #FFE8F3;
           }

            .popOut .homeSpeech.feast {
                background: #f1d9e7 url(../images/present.png) center 90% no-repeat;
                height: 190px;
           }

