                -o-border-radius: 5px;
                -khtml-border-radius: 5px;
                -ms-border-radius: 5px;
                border-radius: 5px;
                color: #000;
                overflow-x: auto;
                border: 1px solid #ccc;
           }

            .boxshadow .pre,
