                border: 0 none;
                -webkit-box-shadow: inset 0 0 10px rgba(0,0,0,0.3);
                -moz-box-shadow: inset 0 0 10px rgba(0,0,0,0.3);
                -o-box-shadow: inset 0 0 10px rgba(0,0,0,0.3);
                -khtml-box-shadow: inset 0 0 10px rgba(0,0,0,0.3);
