            .object {display: block; padding-left: 20px; overflow: hidden; height: auto; position: relative;}

            .array {display: block; padding-left: 20px; overflow: hidden; height: auto; position: relative;}

            .object .toggleBtn,
            .array .toggleBtn {
                position: absolute; left: -10px; top: 0px; display: block; width: 10px; height: 10px;
           }

            .info {
                border: 1px solid #feef97;
                background-color: #fdf8da;
                background: -moz-linear-gradient(top, #fdf8da, #fdf2b4);
                background: -webkit-gradient(linear, left top, left bottom, from(#fdf8da), to(#fdf2b4));
                filter: progid:DXImageTransform.Microsoft.Gradient(StartColorStr='#fdf8da', EndColorStr='#fdf2b4', GradientType=0);
                padding: 5px;
                text-shadow: 0 1px 1px #fff;
                -moz-border-radius: 3px;
                -o-border-radius: 3px;
                -khtml-border-radius: 3px;
                -ms-border-radius: 3px;
                border-radius: 3px;
           }

            .badge {
                border: 1px solid #c0c0c0;
                color: #333;
                background-color: #f9f9f9;
                background: -moz-linear-gradient(top, #f9f9f9, #c0c0c0);
                background: -webkit-gradient(linear, left top, left bottom, from(#f9f9f9), to(#c0c0c0));
                filter: progid:DXImageTransform.Microsoft.Gradient(StartColorStr='#f9f9f9', EndColorStr='#c0c0c0', GradientType=0);
                padding: 5px;
                text-shadow: none;
                -moz-border-radius: 3px;
                -o-border-radius: 3px;
                -khtml-border-radius: 3px;
                -ms-border-radius: 3px;
                border-radius: 3px;
                font-size: 14px;
                text-shadow: 0 1px 0 #fff;
                -webkit-box-shadow: inset 0 0 1px #fff;
                -moz-box-shadow: inset 0 0 1px #fff;
                -o-box-shadow: inset 0 0 1px #fff;
                -khtml-box-shadow: inset 0 0 1px #fff;
                -ms-box-shadow: inset 0 0 1px #fff;
                box-shadow: inset 0 0 1px #fff;
                position: relative;
                padding-left: 46px;
                line-height: 16px;
           }
            .badge:after {
                content: ' ';
                width: 42px; height: 100%;
                display: block;
                background: url(../images/license.png) -10px center no-repeat;
                position: absolute; left: 0; top: 0;
                opacity: 0.5;
           }
            .badge a {
                color: #000;
                text-shadow: none;
                font-weight: bold;
                display: block;
                margin-bottom: 4px;
           }

            .defaultCursor {cursor: default;}

            .closed {height: 5px;}

            .logo {text-indent: -9999px; background: url(../images/logo.png) center no-repeat; width: 100px; height: 36px; float: left; margin: 9px 0 0; display: block;}

            /* Tables */
            .tbl {
