.fullscreen-container .widget-panel-inner {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.fullscreen-widget-container {
  display: none;
}

.fullscreen-widget-container:-webkit-full-screen {
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(51,0,102);
  margin: 0 !important;
}

.fullscreen-widget-container:-moz-full-screen {
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(51,0,102);
  margin: 0 !important;
}

.fullscreen-widget-container:-ms-fullscreen {
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(51,0,102);
  margin: 0 !important;
}

.fullscreen-widget-container:full-screen {
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(51,0,102);
  margin: 0 !important;
}

.fullscreen-widget-container:fullscreen {
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(51,0,102);
  margin: 0 !important;
}


/* Header */
.site-header {
    position: fixed;
    width: 100%;
    background: #efefef;
    background: rgba(239,239,239,0.95);
    box-shadow: 0 1px #EF308F, 0 5px 10px rgba(0,0,0,0.2);
    z-index: 450;
    font-size: 16px;
}

.site-logo {
    float: left;
    position: relative;
    height: 4rem;
    line-height: 4rem;
}

.site-logo img {
    vertical-align: middle;
}

/* Navigation */
.navbar-links {
    float: left;
    margin-left: 1rem;
}

.nav > li {
    float: left;
    position: relative;
}

.navbar-links .nav {
    margin-top: 1.85rem;
}

.navbar-links .nav > li {
    border-left: 1px solid #fff;
    border-right: 1px solid #ddd;
}

.navbar-links .nav > li:first-child {
    border-left: 0;
}

.navbar-links .nav > li:last-child {
    border-right: 0;
}

.navbar-links .nav a {
    display: block;
    padding: 0.25rem 0.75rem 0.5rem;
    font-size: 1.125rem;
}

.navbar-links .nav a:hover,
.navbar-links .active-link > a {
    color: #fc0088;
}

.navbar-tools {
    float: right;
    height: 36px;
    margin: 0.8rem 1rem 0 0;
}

.navbar-login {
    float: right;
    position: relative;
    padding: 0.25rem 1rem;
    border: 1px solid #EF308F;
    border-radius: 5px;
    font-size: 1rem;
}

.navbar-api-explorer-btn {
    float: right;
    margin: 0.8rem 0 0;
    border: 1px solid #684090;
    background-color: #8e5dbf;
    background: linear-gradient(top, #8e5dbf, #3e126a);
}

/* User profile nav */
.navbar-user {
    position: relative;
}

.profile-picture {
    float: right;
    width: 1.75rem;
    height: 1.75rem;
    margin: 0.5rem 0 0 0.5rem;
    border-radius: 50%;
}

.user-avatar {
    display: block;
    padding-right: 6px;
}

.user-name {
    float: left;
    margin: 0.75rem 0 0;
}

/* Generic */
.container:before,
.container:after,
.nav:before,
.nav:after {
    content: " ";
    display: table;
}

.container:after,
.nav:after {
    clear: both;
}

.container {
    min-width: 960px;
    margin: 0 auto;
    padding: 0 7%;
}

/* Dropdowns */
.has-dropdown-menu {
    position: relative;
}

.dropdown-menu {
    position: absolute;
    top: 90%;
    left: 50%;
    display: none;
    width: 12rem;
    margin-left: -6rem;
    padding: 1rem;
    border: 1px solid #fc0088;
    border-radius: 4px;
    background: #eee;
    box-shadow: 0 1px 10px rgba(0,0,0,0.2);
    line-height: 2.25rem;
    box-sizing: border-box;
    text-align: left;
    z-index: 1050;
}

.dropdown-menu a {
    display: block;
}

.dropdown-menu a:hover {
    color: #fc0088;
}

.navbar-links .dropdown-menu a {
    display: block;
    padding: 0;
    height: auto;
}

.dropdown-menu-large {
    width: 20rem;
    margin-left: -10rem;
}

.dropdown-menu:after {
    content: "";
    position: absolute;
    top: -7px;
    left: 50%;
    display: block;
    margin-left: -3px;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fc0088;
}

.user-dropdown-menu {
    top: 2.5rem;
    left: auto;
    right: 0;
}

.user-dropdown-menu:after {
    left: auto;
    right: 1.75rem;
}

.logged-in {
    cursor: pointer;
}

/* Submenu */
.submenu {
    position: absolute;
    top: 90%;
    left: 50%;
    display: none;
    width: 12rem;
    margin-left: -6rem;
    padding: 1rem;
    border: 1px solid #fc0088;
    border-radius: 4px;
    background: #eee;
    box-shadow: 0 1px 10px rgba(0,0,0,0.2);
    line-height: 2.25rem;
    box-sizing: border-box;
    text-align: left;
    z-index: 1050;
}

.submenu:after {
    content: "";
    position: absolute;
    top: -7px;
    left: 50%;
    display: block;
    margin-left: -3px;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fc0088;
}

.active-link .submenu {
    display: block;
}

.active-link > a {
    background: url("../images/selected.gif") bottom center no-repeat;
}

.submenu .active-link > a {
    background: none;
}

.has-submenu > a {
    background: none;
}

.submenu li {

}

.menu-separator {
    border-bottom: 1px solid #ccc;
    margin: 10px 0;
}

.apps-dropdown .application-link {
    line-height: 1.5;
}

.menu-title h4 {
    margin: 0;
}
